body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: auto;
  margin: 0;
  background: url("https://previews.123rf.com/images/antonmedvedev/antonmedvedev1907/antonmedvedev190701749/127772789-beautiful-shining-abstract-background-glitter-lights-with-light-bulbs-and-falling-snow-flakes-fly-de.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
